<template>
  <div class="chart-container">
    <h3>Home</h3>
    <div id="c1"></div>
  </div>
</template>
<script>
import { Chart } from '@antv/g2'
export default {
  mounted() {
    this.$nextTick(() => {
      const data = [
        { name: '', score: 30 },
        { name: '', score: 66 },
        { name: '', score: 50 },
        { name: '', score: 84 },
        { name: '', score: 96 }
      ]
      const chart = new Chart({
        container: 'c1',
        width: '100%',
        height: 300
      })

      chart.data(data) // 
      chart.interval().position('name*score') // 
      chart.render() // 
      window.chart = chart
    })
  }
}
</script>
<style lang="scss" scoped>
.chart-container {
  background-color: #fefefe;
  padding: 40px 20px;
}
#c1 {
  border: 2px solid #666;
}
</style>
